@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600);
*,
*::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 42.5em) {
    html {
      font-size: 56.25%; } }

body {
  font-family: 'Source Sans Pro', sans-serif;
  line-height: 1;
  color: #333;
  background: #fff;
  overflow-x: hidden; }

ul {
  list-style: none; }

a,
button {
  -webkit-tap-highlight-color: transparent; }

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  -webkit-appearance: none; }

::-moz-selection {
  background: #4fa7ff;
  color: #fff; }

::selection {
  background: #4fa7ff;
  color: #fff; }

.btn-primary {
  cursor: pointer;
  background: none;
  border: 1px solid #c5cdd3;
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 600;
  color: #8e979e;
  padding: 1.25rem 2.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }
  .btn-primary:hover {
    border: 1px solid #8e979e; }
  .btn-primary svg {
    display: block;
    width: 1.8rem;
    margin-right: 1rem;
    stroke: #8e979e; }

.container {
  width: 100%;
  max-width: 125rem;
  margin: auto;
  padding: 5rem; }
  @media only screen and (max-width: 42.5em) {
    .container {
      padding: 1.5rem; } }

#content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 5rem; }
  @media only screen and (max-width: 42.5em) {
    #content {
      grid-template-columns: 1fr;
      grid-gap: 1.5rem; } }

@media only screen and (max-width: 42.5em) {
  .images {
    grid-row: 2/3; } }

.images__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  grid-gap: 1.5rem; }
  @media only screen and (max-width: 22.5em) {
    .images__grid {
      grid-template-columns: 1fr; } }
  .images__grid-item {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    border-radius: 5px;
    overflow: hidden; }
    .images__grid-item::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      -webkit-transform: translateY(-102%);
              transform: translateY(-102%);
      transition: -webkit-transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out; }
    .images__grid-item img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; }
    .images__grid-item svg {
      display: block;
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -20%);
              transform: translate(-50%, -20%);
      stroke: #fff;
      width: 8rem;
      opacity: 0;
      transition: all 0.2s ease-in-out; }
    .images__grid-item:hover::after, .images__grid-item:focus::after {
      -webkit-transform: translateY(0);
              transform: translateY(0); }
    .images__grid-item:hover svg, .images__grid-item:focus svg {
      opacity: 0.7;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%); }

.images button {
  margin: 1.5rem auto; }

.backdrop {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 50; }

.modal {
  width: 50rem;
  max-width: 95%;
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  text-align: center;
  position: fixed;
  z-index: 55;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .modal h3 {
    font-size: 2.8rem;
    font-weight: 600;
    color: #333;
    padding: 1rem;
    background: #4fa7ff; }
  .modal p {
    font-size: 1.6rem;
    color: #333;
    padding: 1.5rem; }
  .modal button {
    margin: 1.5rem auto; }

@media only screen and (max-width: 42.5em) {
  .queries {
    grid-row: 1/2; } }

.queries button {
  display: block;
  width: 100%;
  background: none;
  border: none;
  border-radius: 5px;
  text-align: left;
  font-size: 1.8rem;
  color: #333;
  padding: 1rem 2.5rem;
  margin: 0.5rem 0;
  position: relative; }
  @media only screen and (max-width: 22.5em) {
    .queries button {
      padding: 0.5rem 1.5rem; } }
  .queries button.queries--active {
    background: #4fa7ff;
    color: #fff; }
  .queries button span {
    display: none; }
  .queries button:hover {
    cursor: pointer;
    background: #f9f9f9; }
    .queries button:hover span {
      display: block;
      background: none; }
      .queries button:hover span svg {
        cursor: pointer;
        display: block;
        width: 2.4rem;
        height: 2.4rem;
        padding: 0.3rem;
        stroke: #8e979e;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        right: 1rem;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        z-index: 10; }
        .queries button:hover span svg:hover {
          background: #4fa7ff;
          stroke: #fff; }
    .queries button:hover.queries--active {
      background: #69b4ff;
      color: #fff; }
      .queries button:hover.queries--active span svg {
        stroke: #fff; }
        .queries button:hover.queries--active span svg:hover {
          background: #fff;
          stroke: #4fa7ff; }

.search {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 5rem;
  margin-bottom: 5rem; }
  @media only screen and (max-width: 57.5em) {
    .search {
      grid-template-columns: 1fr;
      grid-gap: 1.5rem; } }
  @media only screen and (max-width: 42.5em) {
    .search {
      margin-bottom: 1.5rem; } }
  .search__container {
    position: relative; }
    .search__container input {
      width: 100%;
      font-size: 1.8rem;
      padding: 1rem;
      border: 1px solid #c5cdd3;
      border-radius: 5px; }
      .search__container input:focus {
        border: 1px solid #8e979e;
        background: #f9f9f9;
        outline: none; }
    .search__container button {
      display: block;
      cursor: pointer;
      border: none;
      border-radius: 50%;
      background: none;
      width: 2.4rem;
      height: 2.4rem;
      position: absolute;
      top: 50%;
      right: 1rem;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
      .search__container button svg {
        display: block;
        width: 100%;
        height: 100%;
        padding: 0.3rem;
        stroke: #8e979e; }
      .search__container button:hover {
        background: #4fa7ff; }
        .search__container button:hover svg {
          stroke: #fff; }
  .search__actions {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem; }
    @media only screen and (max-width: 22.5em) {
      .search__actions {
        grid-template-columns: 1fr; } }

.spinner {
  margin: 1rem auto;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  border-width: 3px;
  border-style: solid;
  border-color: #4fa7ff #4fa7ff #4fa7ff transparent;
  -webkit-animation: spinner 0.75s linear infinite;
          animation: spinner 0.75s linear infinite; }

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.title {
  font-size: 1.4rem;
  font-weight: 600;
  color: #8e979e;
  text-transform: uppercase;
  padding: 1rem 2rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #c5cdd3;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  .title svg {
    display: block;
    width: 1.8rem;
    height: 1.8rem;
    margin-right: 1rem;
    stroke: #8e979e; }

